<template>
    <div class="overflow-hidden max-w-100 calendar">
        <div class="calendar__header">
            <button class="chevron_left" @click="handleScroll('left')">
                <svg-icon type="mdi" :path="chevron" :size="35"/>
            </button>
            <div class="calendar__date" id="calendar__date">
                <div class="date">{{ date }}</div>
            </div>
            <button class="chevron_right" @click="handleScroll('right')">
                <svg-icon type="mdi" :path="chevron" :size="35"/>
            </button>
        </div>
        <div class="scroll-containe__overflow-auto">
            <div class="scroll-container">
                <ul class="calendar-dates">
                    <li v-for="calendar_date of calendar_dates" :key="calendar_date.day_month"
                        class="calendar-dates__item" @click="select_date($event, calendar_date.date)">
                        <div v-if="now_date === calendar_date.date" class="calendar-dates__data calendar-dates__active">
                            <div class="calendar-dates__item-day">{{ calendar_date.day_month }}</div>
                            <div class="calendar-dates__item-day_week error-text"
                                 v-if="calendar_date.day_week === 'Sat' || calendar_date.day_week === 'Sun'">
                                {{ calendar_date.day_week }}
                            </div>
                            <div class="calendar-dates__item-day_week" v-else>{{ calendar_date.day_week }}</div>
                        </div>
                        <div v-else class="calendar-dates__data">
                            <div class="calendar-dates__item-day">{{ calendar_date.day_month }}</div>
                            <div class="calendar-dates__item-day_week error-text"
                                 v-if="calendar_date.day_week === 'Sat' || calendar_date.day_week === 'Sun'">
                                {{ calendar_date.day_week }}
                            </div>
                            <div class="calendar-dates__item-day_week" v-else>{{ calendar_date.day_week }}</div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import SvgIcon from '@jamescoyle/vue-icon'
import * as moment from 'moment';

import {
    mdiChevronDown,
} from '@mdi/js'

export default {
    name: 'Calendar',
    components: {SvgIcon},
    data: () => ({
        timerID: 0,
        week: ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'],
        date: '',
        now_date: '',
        chevron: mdiChevronDown,
        calendar_dates: [],
        selected_calendar_date: false,
        scroll_shift: 0
    }),
    methods: {
        now_date_time() {
            this.timerID = setInterval(this.updateTime, 1000)
            this.updateTime()
        },
        zeroPadding(num, digit) {
            let zero = ''
            for (let i = 0; i < digit; i++) {
                zero += '0'
            }
            return (zero + num).slice(-digit)
        },
        updateTime() {
            let cd = new Date()
            this.date = this.zeroPadding(cd.getDate(), 2) + '.' + this.zeroPadding(cd.getMonth() + 1, 2) + '.' + this.zeroPadding(cd.getFullYear(), 4) + ' ' + this.week[cd.getDay()]
        },
        init() {
            this.now_date = moment().format('DD.MM.YYYY')
            this.now_date_time()
            moment.locale('en')
            for (let n = 0; n < 26; n++) { // add 26 days next
                this.calendar_dates.push(
                    {
                        day_month: moment().add(n, 'days').format('DD'),
                        day_week: moment().add(n, 'days').format('dddd').substring(0, 3),
                        date: moment().add(n, 'days').format('DD.MM.YYYY')
                    }
                )
            }
            for (let n = 0; n < 5; n++) { // add 3 days previous
                if (this.now_date !== moment().subtract(n, 'days').format('DD.MM.YYYY')) {
                    this.calendar_dates.unshift(
                        {
                            day_month: moment().subtract(n, 'days').format('DD'),
                            day_week: moment().subtract(n, 'days').format('dddd').substring(0, 3),
                            date: moment().subtract(n, 'days').format('DD.MM.YYYY')
                        }
                    )
                }
            }
            console.log(this.calendar_dates)
        },
        handleScroll(direction) {
            let scroll_container = document.querySelector('.scroll-container')
            if (this.scroll_shift < 1600 && direction === 'right') {
                this.scroll_shift += 400
                scroll_container.scroll({
                    left: this.scroll_shift,
                    top: 0,
                    behavior: 'smooth'
                })
            } else if (this.scroll_shift > 10 && direction === 'left') {
                this.scroll_shift -= 400
                scroll_container.scroll({
                    left: this.scroll_shift,
                    top: 0,
                    behavior: 'smooth'
                })
            }
        },
        select_date(e, date) {
            console.log(e.currentTarget)
            console.log(e.currentTarget.firstElementChild)
            console.log(date)

            let calendar_dates__item = document.querySelectorAll('.calendar-dates__item')
            calendar_dates__item.forEach( item => {
                if(item.firstElementChild.classList.contains('calendar-dates__active')) {
                    item.firstElementChild.classList.remove('calendar-dates__active')
                }
            })
            e.currentTarget.firstElementChild.classList.add('calendar-dates__active')
        }
    },
    mounted() {
        this.init()
    },
}
</script>

