                            </div>
                            <div class="tasks__wrap-title-description">
                                <div v-if="item.title" class="tasks__title">{{ item.title }}</div>
                                <div v-if="item.description" class="tasks__description">{{ item.description }}</div>
                            </div>
                            <div v-if="item.time" class="tasks__time">{{ item.time }}</div>
                            <div v-if="item.priority" class="tasks__priority">
                                <svg-icon type="mdi" :path="path_flag" :size="18" class="color_white"/>
                                {{ item.priority }}
                            </div>
                        </div>
                    </div>
                </div>
                <div class="tasks_show" @click="show_tasks_completed = !show_tasks_completed">
                    <h2>Completed</h2>
                    <svg-icon type="mdi" :path="path_chevron" :size="16" class="color_white"
                              :class="{chevron_active: show_tasks_completed}"/>
                </div>
                <div class="tasks w-100" v-show="show_tasks_completed">
                    <div v-for="(item, index) in tasks" :key="item.title + index">
                        <div v-if="item.completed" class="tasks__item">
                            <div class="menu-task" :id="`task_${item.id}`">
                                <button type="button" class="menu-task__btn menu-task__btn-back"
                                        @click="hide_menu_task(item.id)">
                                    <svg-icon type="mdi" :path="path_chevron" :size="40" class="chevron_back"/>
                                </button>
                                <button type="button" class="menu-task__btn error"
