                                    <svg-icon type="mdi" :path="path_check" :size="18" class="error"/>
                                    <span class="menu-task__btn-text">Not done</span>
                                </button>
                                <button type="button" class="menu-task__btn primary" @click="edit_task(item.id)">
                                    <svg-icon type="mdi" :path="path_pencil" :size="18" class="primary"/>
                                    <span class="menu-task__btn-text">Edit</span>
                                </button>
                                <button type="button" class="menu-task__btn error" @click="delete_task(item.id)">
                                    <svg-icon type="mdi" :path="path_delete" :size="18" class="error"/>
                                    <span class="menu-task__btn-text">Delete</span>
                                </button>
                            </div>
                            <div class="tasks__dots" @click="show_menu_task(item.id)">
                                <svg-icon type="mdi" :path="path_dots" :size="25" class="color_white"/>
                            </div>
                            <div v-if="item.tag && item.tag !== 'null'">
                                <div class="tasks__item-icon" :style="`background: ${JSON.parse(item.tag).bg}`">
                                    <svg-icon type="mdi" :path="JSON.parse(item.tag).path" :size="40"
                                              :color="JSON.parse(item.tag).color"/>
                                </div>
                            </div>
                            <div class="tasks__wrap-title-description">
                                <div v-if="item.title" class="tasks__title">{{ item.title }}</div>
                                <div v-if="item.description" class="tasks__description">{{ item.description }}</div>
                            </div>
                            <div v-if="item.time" class="tasks__time">{{ item.time }}</div>
                            <div v-if="item.priority" class="tasks__priority">
                                <svg-icon type="mdi" :path="path_flag" :size="18" class="color_white"/>
                                {{ item.priority }}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <bottom-menu @show_form_task="show_form_task"/>
        </div>
        <div class="step_tag" v-if="open_tag">
            <div class="container_500">
                <div class="tag">
                    <div class="tag__head">Task Tag</div>
                    <ul class="tag__list">
                        <li class="tag__item" v-for="tag in tags">
                            <button type="button" @click="task.tag = JSON.stringify(tag); open_tag = false"
                                    :style="`background: ${tag.bg};`" class="tag__item-btn">
                                <svg-icon type="mdi" :path="tag.path" :size="27" :color="tag.color"></svg-icon>
                            </button>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="step_priority" v-if="open_priority">
            <div class="container_500">
                <div class="priority">
                    <div class="priority__head">Task Priority</div>
                    <ul class="priority__list">
                        <li class="priority__item" v-for="n in 12">
                            <button type="button" @click="task.priority = n; open_priority = false"
                                    class="priority__item-btn">
                                <img src="../../assets/flag.svg" alt="flag">
                                <span class="priority__item-integer">{{ n }}</span>
                            </button>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="step_calendar" v-if="open_date_timepicker">
            <div class="container_500">
                <date-picker v-model="task.time"
                             type="datetime"
                             popup-class="datetime_picker"
                             :format="'DD.MM.YYYY HH:mm'"
                             :title-format="'DD.MM.YYYY HH:mm'"
                             :hour-step="1"
                             :minute-step="1"
                             :show-hour="true"
                             :show-minute="true"
                             :show-second="false"
                             :use12h="false"
                             :open.sync="open_date_timepicker"
                             valueType="format"
                             prefix-class="xmx"
                             placeholder="Select datetime"
                             @change="handleChange"></date-picker>
            </div>
        </div>
        <div class="form-task" v-if="form_task">
            <div class="container_500">
                <div class="row max-w-100">
                    <div class="col col-12">
                        <div class="form-task__h2 justify-content-between d-flex align-items-center">
                            <span>Add Task</span>
                            <div class="data-choose">
                                <div v-if="task.time" class="data-choose__item">
                                    <svg-icon type="mdi" :path="path_timer" :size="18" class="color_active"></svg-icon>
                                    {{ task.time }}
                                </div>
                                <div v-if="task.time && task.priority" class="data-choose__item">&nbsp;&nbsp;&bull;&nbsp;</div>
                                <div v-if="task.priority" class="data-choose__item">
                                    <svg-icon type="mdi" :path="path_flag" :size="18" class="color_active"></svg-icon>
                                    {{ task.priority }}
                                </div>
                                <div v-if="task.tag && task.priority || task.time" class="data-choose__item">&nbsp;&nbsp;&bull;&nbsp;</div>
                                <div v-if="(task.tag && task.tag.bg) || JSON.parse(task.tag)" class="data-choose__item">
                                    <span :style="`background: ${JSON.parse(task.tag).bg}`" class="tag__selected">
                                        <svg-icon type="mdi" :path="task.tag.path || JSON.parse(task.tag).path"
                                                  :size="20"
                                                  :color="task.tag.color || JSON.parse(task.tag).color"></svg-icon>
                                    </span>
                                </div>
                                <div class="data-choose__item" v-else></div>
                            </div>
                        </div>
                        <div class="form-task__wrap-input">
                            <input type="text" v-model="task.title"
                                   placeholder="Title task"
                                   class="form-task__input"
                                   required>
                        </div>
                        <div class="form-task__wrap-textarea">
                            <textarea v-model="task.description"
                                      rows="2"
                                      placeholder="Description task"
                                      class="form-task__description"></textarea>
                        </div>
                    </div>
                </div>
                <div class="d-flex flex-nowrap">
                    <div class="flex-item pl-0" @click="show_datetimepicker" title="Date time choose">
                        <svg-icon type="mdi" :path="path_timer" :size="27"
                                  :class="{ color_active: task.time }" class="color_white"></svg-icon>
                    </div>
                    <div class="flex-item" title="Priority choose" @click="show_priority_step">
                        <svg-icon type="mdi" :path="path_flag" :size="27" class="color_white"
                                  :class="{ color_active: task.priority }"></svg-icon>
                    </div>
                    <div class="flex-item" title="Category choose" @click="show_tag_step">
                        <svg-icon type="mdi" :path="path_tag" :size="27" class="color_white"
                                  :class="{ color_active: task.tag }"></svg-icon>
                    </div>
                    <div class="flex-item flex-right" @click="saveTask" title="Create task">
                        <button type="button" class="btn_save_task">Save</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="form-bg" @click="hide_form_task" v-if="form_task"></div>
    </div>
</template>

<script>
import TopMenu from "../../components/ui/top-menu.vue"
import BottomMenu from "../../components/ui/bottom-menu.vue"
import SvgIcon from '@jamescoyle/vue-icon'
import {
    mdiTimerOutline,
    mdiTagOutline,
    mdiFlagVariantOutline,
    mdiChevronDown,
    mdiMusic,
    mdiCameraOutline,
    mdiCartVariant,
    mdiChatProcessingOutline,
    mdiControllerClassicOutline,
    mdiHeartPulse,
    mdiHomeOutline,
    mdiIsland,
    mdiKettle,
    mdiLightbulbOnOutline,
    mdiPhoneOutline,
    mdiTruckFastOutline,
    mdiBookOpenPageVariantOutline,
    mdiBabyFaceOutline,
    mdiBasketball,
    mdiBriefcaseVariantOutline,
    mdiCheckUnderline,
    mdiNoteEditOutline,
    mdiDeleteEmptyOutline,
    mdiDotsVertical,
} from '@mdi/js'
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
import Task from "../../models/Task";
import Calendar from "../../components/ui/calendar.vue";

export default {
    name: 'Home',
    components: {SvgIcon, TopMenu, BottomMenu, DatePicker, Calendar},
    data: () => ({
        path_timer: mdiTimerOutline,
        path_tag: mdiTagOutline,
        path_flag: mdiFlagVariantOutline,
        path_chevron: mdiChevronDown,
        path_dots: mdiDotsVertical,
        path_check: mdiCheckUnderline,
        path_pencil: mdiNoteEditOutline,
        path_delete: mdiDeleteEmptyOutline,
        task: {
            completed: false,
            time: null,
            priority: null,
            tag: null,
        },
        tasks: [],
        form_task: false,
        open_date_timepicker: false,
        open_priority: false,
        open_tag: false,
        tags: [
            {path: mdiBriefcaseVariantOutline, color: "#0ba300", bg: "#9bff7f"},
            {path: mdiMusic, color: "#A000A3", bg: "#FC7FFF"},
            {path: mdiCameraOutline, color: "#0026a3", bg: "#7f9dff"},
            {path: mdiCartVariant, color: "#2ea300", bg: "#aeff7f"},
            {path: mdiChatProcessingOutline, color: "#00a39e", bg: "#7fe1ff"},
            {path: mdiControllerClassicOutline, color: "#a30000", bg: "#ff7f7f"},
            {path: mdiHeartPulse, color: "#1ea300", bg: "#90ff7f"},
            {path: mdiHomeOutline, color: "#a30044", bg: "#ff7fb9"},
            {path: mdiIsland, color: "#a34400", bg: "#ffcc7f"},
            {path: mdiKettle, color: "#006aa3", bg: "#7fe8ff"},
            {path: mdiLightbulbOnOutline, color: "#a35700", bg: "#ffd67f"},
            {path: mdiPhoneOutline, color: "#a32b00", bg: "#ff9d7f"},
            {path: mdiBabyFaceOutline, color: "#a37500", bg: "#fff47f"},
            {path: mdiTruckFastOutline, color: "#00a326", bg: "#8cff7f"},
            {path: mdiBookOpenPageVariantOutline, color: "#98a300", bg: "#fff97f"},
            {path: mdiBasketball, color: "#0065a3", bg: "#7fd9ff"}
        ],
        show_tasks_active: true,
        show_tasks_completed: false,
    }),
    methods: {
        show_form_task() {
            this.form_task = true
        },
        hide_form_task() {
            this.form_task = false
            this.closeAll()
            this.task = {
                title: null,
                description: null,
                time: null,
                priority: 0,
                tag: null
            }
        },
        handleChange(value, type) {
            if (type === 'minute') {
                this.open_date_timepicker = false;
            }
        },
        closeAll() {
            this.open_priority = false
            this.open_date_timepicker = false
            this.open_tag = false
        },
        show_priority_step() {
            this.closeAll()
            this.open_priority = true
        },
        show_datetimepicker() {
            this.closeAll()
            this.open_date_timepicker = true
        },
        show_tag_step() {
            this.closeAll()
            this.open_tag = true
        },
        async saveTask() {
            let exists_task = false
            this.tasks.forEach(item => {
                // Update Task
                if (item.id === this.task.id) {
                    item.id = this.task.id
                    item.completed = this.task.completed
                    item.title = this.task.title
                    item.description = this.task.description
                    item.time = this.task.time
                    item.priority = this.task.priority
                    item.tag = this.task.tag
                    this.update_task(item)
                    exists_task = true
                }
            })
            // New Task
            if (!exists_task) {
                let result = new Task(this.task)
                await result.save()
                this.init()
            }
            this.closeAll()
            this.hide_form_task()
        },
        sort_tasks() {
            return this.tasks.sort((a, b) => a.priority - b.priority)
        },
        async init() {
            this.task = {
                title: null,
                description: null,
                time: null,
                priority: 0,
                tag: null
            }
            this.tasks = await new Task().get()
            this.sort_tasks()
        },
        show_menu_task(id) {
            let el = document.getElementById('task_' + id)
            el.classList.add('menu-task-active')
            el.style.display = 'flex'
        },
        hide_menu_task(id) {
            let el = document.getElementById('task_' + id)
            el.classList.remove('menu-task-active')
            setTimeout(() => el.style.display = 'none', 400)
        },
        async delete_task(id) {
            this.tasks.forEach(item => {
                if (item.id === id) {
                    this.hide_menu_task(id)
                    this.delete_task_DB(item)
                }
            })
        },
        async delete_task_DB(item) {
            let result = new Task(item)
            result = await result.delete()
            if (result.status === 200) this.init()
        },
        async update_task(task_data) {
            let result = new Task(task_data)
            result = await result.save()
            if (result.status === 200) this.init()
        },
        task_completed(id) {
            this.tasks.forEach(item => {
                if (item.id === id) {
                    item.completed = true
                    this.update_task(item)
                }
            })
        },
        task_not_completed(id) {
            this.tasks.forEach(item => {
                if (item.id === id) {
                    item.completed = false
                    this.update_task(item)
                }
            })
        },
        edit_task(id) {
            this.tasks.forEach(item => {
                if (item.id === id) {
                    this.task = {
                        title: null,
                        description: null,
                        time: null,
                        priority: 0,
                        tag: null
                    }
                    this.hide_menu_task(id)
                    this.task.id = item.id
                    this.task.completed = item.completed
                    this.task.title = item.title
                    this.task.description = item.description
                    this.task.time = item.time
                    this.task.priority = item.priority
                    this.task.tag = item.tag
                    this.show_form_task()
                    this.sort_tasks()
                }
            })
        },
    },
    mounted() {
        this.init()
        //setInterval(() => console.log(this.tasks), 6000)
        //setInterval(() => console.log(this.task), 6000)
    },
}
</script>

