J","sourcesContent":["@import './var.scss';\n\n.#{$namespace}-icon-left:before,\n.#{$namespace}-icon-right:before,\n.#{$namespace}-icon-double-left:before,\n.#{$namespace}-icon-double-right:before,\n.#{$namespace}-icon-double-left:after,\n.#{$namespace}-icon-double-right:after {\n  content: '';\n  position: relative;\n  top: -1px;\n  display: inline-block;\n  width: 10px;\n  height: 10px;\n  vertical-align: middle;\n  border-style: solid;\n  border-color: currentColor;\n  border-width: 2px 0 0 2px;\n  border-radius: 1px;\n  box-sizing: border-box;\n  transform-origin: center;\n  transform: rotate(-45deg) scale(0.7);\n}\n\n.#{$namespace}-icon-double-left:after {\n  left: -4px;\n}\n\n.#{$namespace}-icon-double-right:before {\n  left: 4px;\n}\n\n.#{$namespace}-icon-right:before,\n.#{$namespace}-icon-double-right:before,\n.#{$namespace}-icon-double-right:after {\n  transform: rotate(135deg) scale(0.7);\n}\n","\n@import '../../../scss/variables';\n\n$namespace: 'xmx';\n\n@import '~vue2-datepicker/scss/index.scss';\n\n$default-color: $color-bg;\n$primary-color: $color-primary;\n\n.xmx-time,\n.xmx-calendar {\n    background: $default-color;\n\n    .cell.active {\n        background: lighten($default-color, 10);\n        color: $primary-color\n    }\n\n    .cell.today,\n    .xmx-time-item.active {\n        color: $primary-color\n    }\n\n    .xmx-btn:hover {\n        color: $primary-color\n    }\n}\n\n:root {\n    --font-family-sans-serif: $ff-regular;\n    --font-family-monospace: $ff-bold;\n}\n\nhtml,\nbody {\n    background-color: $body-bg;\n    font-family: $ff-regular;\n    color: #fff;\n    box-sizing: border-box;\n    overflow: hidden;\n    user-select: none;\n}\n\n.container {\n    margin-top: 0;\n}\n\n.container_500 {\n    width: 500px;\n    display: block;\n    margin: 0 auto;\n    max-width: 100%;\n}\n\n.home__container {\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    align-items: center;\n    flex-wrap: wrap;\n    max-width: 100%;\n    margin: 0 auto;\n    position: relative;\n    z-index: 0;\n}\n\n.max-w-100 {\n    max-width: 100%\n}\n\n/* Form title */\n.form-bg {\n    z-index: 2;\n    position: fixed;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    top: 0;\n    background: rgba(5, 5, 5, 0.95);\n}\n\n.form-task {\n    max-width: 100%;\n    position: fixed;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    background: $color-bg;\n    padding: 25px;\n    border-radius: 16px 16px 0 0;\n    z-index: 10;\n\n    .form-task__h2 {\n        min-width: 100%;\n        font-style: normal;\n        font-weight: 700;\n        font-size: 20px;\n        line-height: 150.5%;\n        color: rgba(255, 255, 255, 0.87);\n        margin-bottom: 14px;\n\n\n    }\n\n    .form-task__wrap-textarea,\n    .form-task__wrap-input {\n        margin-bottom: 15px;\n        max-width: 100%;\n    }\n\n    .form-task__input {\n        max-width: 100%;\n        width: 100%;\n        height: 43px;\n        border: 1px solid $color-border;\n        border-radius: 4px;\n        font-style: normal;\n        font-weight: 400;\n        font-size: 18px;\n        line-height: 150.5%;\n        color: $color-input-text;\n        padding: 5px 8px;\n        background: $color-bg;\n        box-sizing: border-box;\n    }\n\n    .form-task__description {\n        max-width: 100%;\n        width: 100%;\n        border: 1px solid $color-border;\n        border-radius: 4px;\n        font-style: normal;\n        font-weight: 400;\n        font-size: 18px;\n        line-height: 150.5%;\n        color: $color-input-text;\n        padding: 5px 8px;\n        background: $color-bg;\n        box-sizing: border-box;\n    }\n}\n\n.tag__selected {\n    width: 23px;\n    height: 23px;\n    margin-left: 3px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    border-radius: 4px;\n}\n\n.flex-item {\n    padding: 5px 15px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.flex-right {\n    margin-left: auto;\n}\n\n.pl-0 {\n    padding-left: 0;\n}\n\n.home {\n    height: 100%;\n    max-width: 100%;\n}\n\n.datetime_picker {\n    position: fixed !important;\n    top: calc(30% - 124px) !important;\n    left: calc(50% - 136px) !important;\n    border-radius: 4px;\n}\n\n/* Priority */\n.step_priority {\n    position: fixed !important;\n    top: calc(30% - 145px) !important;\n    left: calc(50% - 163px) !important;\n    z-index: 20;\n    width: 327px;\n    height: 290px;\n    border-radius: 4px;\n    background: $color-bg;\n    padding: 11px;\n    border: 1px solid #e8e8e8;\n}\n\n.fz-16 {\n    font-size: 16px;\n}\n\n.font-w-100 {\n    font-weight: 100;\n    font-family: $ff-light;\n}\n\n.priority {\n\n    .priority__head {\n        text-align: center;\n        padding-top: 7px;\n        padding-bottom: 7px;\n        border-bottom: 2px solid $color-border;\n        margin-bottom: 10px;\n    }\n\n    .priority__list {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        flex-wrap: wrap;\n        max-width: 100%;\n        margin-bottom: 10px;\n\n        .priority__item {\n            background: $color-bg-item;\n            color: #fff;\n            padding: 6px;\n            margin: 5px;\n            border-radius: 4px;\n            width: 64px;\n            height: 64px;\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            flex-wrap: wrap;\n            text-align: center;\n\n            &:hover {\n                background: darken($color-bg-item, 10);\n            }\n\n            .priority__item-btn {\n                border: none;\n                outline: none;\n                background: transparent;\n                width: 100%;\n                height: 100%;\n\n                .priority__item-integer {\n                    min-width: 100%;\n                    display: block;\n                    color: #fff;\n                }\n            }\n        }\n    }\n}\n\n/* Tag */\n.step_tag {\n    position: fixed !important;\n    top: calc(30% - 145px) !important;\n    left: calc(50% - 163px) !important;\n    z-index: 20;\n    width: 327px;\n    height: 365px;\n    border-radius: 4px;\n    background: $color-bg;\n    padding: 11px;\n    border: 1px solid #e8e8e8;\n}\n\n.tag {\n\n    .tag__head {\n        text-align: center;\n        padding-top: 7px;\n        padding-bottom: 7px;\n        border-bottom: 2px solid $color-border;\n        margin-bottom: 10px;\n    }\n\n    .tag__list {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        flex-wrap: wrap;\n        max-width: 100%;\n        margin-bottom: 10px;\n\n        .tag__item {\n            background: $color-bg-item;\n            color: #fff;\n            padding: 6px;\n            margin: 5px;\n            border-radius: 4px;\n            width: 64px;\n            height: 64px;\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            flex-wrap: wrap;\n            text-align: center;\n\n            &:hover {\n                background: darken($color-bg-item, 10);\n            }\n\n            .tag__item-btn {\n                border: none;\n                outline: none;\n                background: transparent;\n                width: 100%;\n                height: 100%;\n\n                .tag__item-integer {\n                    min-width: 100%;\n                    display: block;\n                    color: #fff;\n                }\n            }\n        }\n    }\n}\n\n.justify-content-between {\n    justify-content: space-between;\n}\n\n.color_white {\n    color: white\n}\n\n.color_active {\n    color: $color-primary\n}\n\n.align-items-center {\n    align-items: center;\n}\n\n.data-choose {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    max-width: 75%;\n    font-size: 12px;\n    font-weight: 100;\n    font-family: $ff-light;\n\n    .data-choose__item {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        font-size: 12px;\n        font-weight: 100;\n        font-family: $ff-light;\n    }\n}\n\n.btn_save_task {\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    width: 110px;\n    height: 35px;\n    background: $color-primary;\n    border-radius: 4px;\n    font-size: 16px;\n    line-height: 150.5%;\n    color: #fff;\n    border: none;\n    outline: none;\n}\n\n.tasks_show {\n    padding: 0 5px;\n    height: 30px;\n    background: $color-bg;\n    border-radius: 4px;\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    margin-bottom: 15px;\n    width: fit-content;\n    color: $color-border;\n\n    &:hover {\n        cursor: pointer;\n    }\n}\n\n.tasks-wrap {\n    height: calc(100% - 300px);\n    width: 100%;\n    max-width: 100%;\n    overflow-y: auto;\n\n    h2 {\n        padding-left: 4px;\n        font-size: 12px;\n        font-weight: 100;\n        color: $color-border\n    }\n}\n\n.tasks {\n    max-height: calc(73% - 90px);\n    overflow-y: auto;\n    width: 100%;\n    max-width: 100%;\n    margin-bottom: 10px;\n\n    .tasks__item {\n        background: $color-bg;\n        border-radius: 4px;\n        margin-bottom: 10px;\n        display: flex;\n        justify-content: flex-start;\n        align-items: flex-start;\n        padding: 14px;\n        position: relative;\n\n        .tasks__item-icon {\n            width: 45px;\n            height: 45px;\n            border-radius: 4px;\n            display: flex;\n            justify-content: center;\n            align-items: center;\n        }\n    }\n\n    .tasks__wrap-title-description {\n        display: block;\n        margin-left: 15px;\n        width: calc(100% - 100px);\n\n        .tasks__title {\n            font-size: 17px;\n        }\n\n        .tasks__description {\n            font-size: 15px;\n            font-weight: 100;\n            font-family: $ff-light;\n        }\n    }\n\n    .tasks__time {\n        position: absolute;\n        bottom: 3px;\n        right: 3px;\n        font-weight: 100;\n        font-family: $ff-light;\n        font-size: 12px;\n    }\n\n    .tasks__priority {\n        color: #fff;\n        border: 1px solid $color-primary;\n        border-radius: 4px;\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        font-size: 12px;\n        position: absolute;\n        top: 3px;\n        right: 3px;\n        width: 42px;\n        height: 35px;\n    }\n}\n\n.tasks__dots {\n    height: 44px;\n    display: flex;\n    align-items: center;\n    margin-left: -13px;\n\n    &:hover {\n        cursor: pointer;\n    }\n}\n\n.chevron_active {\n    transform: rotate(180deg) !important;\n}\n\n.menu-task {\n    border-radius: 4px;\n    min-width: 100%;\n    min-height: 100%;\n    background: $color-bg-item;\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: 100;\n    /*display: flex;*/\n    display: none;\n    justify-content: space-around;\n    align-items: center;\n    transform: translateX(-500px);\n    transition: all .4s;\n\n    .menu-task__btn {\n        border: none;\n        outline: none;\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        border-radius: 4px;\n        padding: 7px 10px;\n        cursor: pointer;\n\n        .menu-task__btn-text {\n            margin-left: 3px;\n        }\n    }\n\n    .menu-task__btn-back {\n        background: transparent;\n        border: none;\n        outline: none;\n\n        .chevron_back {\n            color: $color-primary;\n            transform: rotate(90deg);\n        }\n    }\n}\n\n.menu-task-active {\n    transform: translateX(0);\n    transition: all .4s;\n}\n\n.success {\n    background: #D4EDDA;\n    color: #28A745;\n\n    .menu-task__btn-text {\n        color: #28A745;\n    }\n}\n\n.primary {\n    background: #D1ECF1;\n    color: #007BFF;\n\n    .menu-task__btn-text {\n        color: #007BFF;\n    }\n}\n\n.error {\n    background: #F8D7DA;\n    color: $red;\n\n    .menu-task__btn-text {\n        color: $red;\n    }\n}\n","@import './var.scss';\n\n.#{$namespace}-btn {\n  box-sizing: border-box;\n  line-height: 1;\n  font-size: 14px;\n  font-weight: 500;\n  padding: 7px 15px;\n  margin: 0;\n  cursor: pointer;\n  background-color: transparent;\n  outline: none;\n  border: 1px solid rgba(0, 0, 0, 0.1);\n  border-radius: 4px;\n  color: $default-color;\n  white-space: nowrap;\n  &:hover {\n    border-color: $primary-color;\n    color: $primary-color;\n  }\n  &:disabled,\n  &.disabled {\n    color: $disabled-color;\n    cursor: not-allowed;\n  }\n}\n\n.#{$namespace}-btn-text {\n  border: 0;\n  padding: 0 4px;\n  text-align: left;\n  line-height: inherit;\n}\n","$namespace: 'mx' !default;\n\n$default-color: #73879c !default;\n$primary-color: #1284e7 !default;\n\n$today-color: mix(#fff, $primary-color, 10%) !default;\n\n$popup-z-index: 2001 !default;\n\n$input-border-color: #ccc !default;\n$input-color: #555 !default;\n$input-hover-border-color: #409aff !default;\n\n$disabled-color: #ccc !default;\n$disabled-background-color: #f3f3f3 !default;\n\n$border-color: #e8e8e8 !default;\n\n$calendar-active-color: #fff !default;\n$calendar-active-background-color: $primary-color !default;\n\n$calendar-hover-color: $default-color !default;\n$calendar-hover-background-color: mix(#fff, $calendar-active-background-color, 95%) !default;\n\n$calendar-in-range-color: $default-color !default;\n$calendar-in-range-background-color: mix(#fff, $calendar-active-background-color, 85%) !default;\n\n$time-active-color: $primary-color !default;\n$time-active-background-color: transparent !default;\n\n$time-hover-color: $default-color !default;\n$time-hover-background-color: mix(#fff, $calendar-active-background-color, 95%) !default;\n\n$input-border-radius: 4px !default;\n$sidebar-margin-left: 100px !default;\n","@import './var.scss';\n\n.#{$namespace}-scrollbar {\n  height: 100%;\n  &:hover {\n    .#{$namespace}-scrollbar-track {\n      opacity: 1;\n    }\n  }\n}\n\n.#{$namespace}-scrollbar-wrap {\n  height: 100%;\n  overflow-x: hidden;\n  overflow-y: auto;\n}\n\n.#{$namespace}-scrollbar-track {\n  position: absolute;\n  top: 2px;\n  right: 2px;\n  bottom: 2px;\n  width: 6px;\n  z-index: 1;\n  border-radius: 4px;\n  opacity: 0;\n  transition: opacity 0.24s ease-out;\n  .#{$namespace}-scrollbar-thumb {\n    position: absolute;\n    width: 100%;\n    height: 0;\n    cursor: pointer;\n    border-radius: inherit;\n    background-color: rgba(144, 147, 153, 0.3);\n    transition: background-color 0.3s;\n  }\n}\n","@import './var.scss';\n\n.#{$namespace}-zoom-in-down-enter-active,\n.#{$namespace}-zoom-in-down-leave-active {\n  opacity: 1;\n  transform: scaleY(1);\n  transition: transform 0.3s cubic-bezier(0.23, 1, 0.32, 1),\n    opacity 0.3s cubic-bezier(0.23, 1, 0.32, 1);\n  transform-origin: center top;\n}\n\n.#{$namespace}-zoom-in-down-enter,\n.#{$namespace}-zoom-in-down-enter-from,\n.#{$namespace}-zoom-in-down-leave-to {\n  opacity: 0;\n  transform: scaleY(0);\n}\n","@import './var.scss';\n@import './icon.scss';\n@import './btn.scss';\n@import './scrollbar.scss';\n@import './animation.scss';\n\n.#{$namespace}-datepicker {\n  position: relative;\n  display: inline-block;\n  width: 210px;\n  svg {\n    width: 1em;\n    height: 1em;\n    vertical-align: -0.15em;\n    fill: currentColor;\n    overflow: hidden;\n  }\n}\n\n.#{$namespace}-datepicker-range {\n  width: 320px;\n}\n\n.#{$namespace}-datepicker-inline {\n  width: auto;\n}\n\n.#{$namespace}-input-wrapper {\n  position: relative;\n}\n\n.#{$namespace}-input {\n  display: inline-block;\n  box-sizing: border-box;\n  width: 100%;\n  height: 34px;\n  padding: 6px 30px;\n  padding-left: 10px;\n  font-size: 14px;\n  line-height: 1.4;\n  color: $input-color;\n  background-color: #fff;\n  border: 1px solid $input-border-color;\n  border-radius: $input-border-radius;\n  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);\n\n  &:hover,\n  &:focus {\n    border-color: $input-hover-border-color;\n  }\n  &:disabled,\n  &.disabled {\n    color: $disabled-color;\n    background-color: $disabled-background-color;\n    border-color: $input-border-color;\n    cursor: not-allowed;\n  }\n  &:focus {\n    outline: none;\n  }\n  &::-ms-clear {\n    display: none;\n  }\n}\n\n.#{$namespace}-icon-calendar,\n.#{$namespace}-icon-clear {\n  position: absolute;\n  top: 50%;\n  right: 8px;\n  transform: translateY(-50%);\n  font-size: 16px;\n  line-height: 1;\n  color: rgba(0, 0, 0, 0.5);\n  vertical-align: middle;\n}\n\n.#{$namespace}-icon-clear {\n  cursor: pointer;\n  &:hover {\n    color: rgba(0, 0, 0, 0.8);\n  }\n}\n\n.#{$namespace}-datepicker-main {\n  font: 14px/1.5 'Helvetica Neue', Helvetica, Arial, 'Microsoft Yahei', sans-serif;\n  color: $default-color;\n  background-color: #fff;\n  border: 1px solid $border-color;\n}\n\n.#{$namespace}-datepicker-popup {\n  position: absolute;\n  margin-top: 1px;\n  margin-bottom: 1px;\n  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);\n  z-index: $popup-z-index;\n}\n\n.#{$namespace}-datepicker-sidebar {\n  float: left;\n  box-sizing: border-box;\n  width: $sidebar-margin-left;\n  padding: 6px;\n  overflow: auto;\n}\n\n.#{$namespace}-datepicker-sidebar + .#{$namespace}-datepicker-content {\n  margin-left: $sidebar-margin-left;\n  border-left: 1px solid $border-color;\n}\n\n.#{$namespace}-datepicker-body {\n  position: relative;\n  user-select: none;\n}\n\n.#{$namespace}-btn-shortcut {\n  display: block;\n  padding: 0 6px;\n  line-height: 24px;\n}\n\n.#{$namespace}-range-wrapper {\n  display: flex;\n  @media (max-width: 750px) {\n    flex-direction: column;\n  }\n}\n\n.#{$namespace}-datepicker-header {\n  padding: 6px 8px;\n  border-bottom: 1px solid $border-color;\n}\n\n.#{$namespace}-datepicker-footer {\n  padding: 6px 8px;\n  text-align: right;\n  border-top: 1px solid $border-color;\n}\n\n.#{$namespace}-calendar {\n  box-sizing: border-box;\n  width: 248px;\n  padding: 6px 12px;\n  & + & {\n    border-left: 1px solid $border-color;\n  }\n}\n\n.#{$namespace}-calendar-header {\n  box-sizing: border-box;\n  height: 34px;\n  line-height: 34px;\n  text-align: center;\n  overflow: hidden;\n}\n\n.#{$namespace}-btn-icon-left,\n.#{$namespace}-btn-icon-double-left {\n  float: left;\n}\n.#{$namespace}-btn-icon-right,\n.#{$namespace}-btn-icon-double-right {\n  float: right;\n}\n\n.#{$namespace}-calendar-header-label {\n  font-size: 14px;\n}\n\n.#{$namespace}-calendar-decade-separator {\n  margin: 0 2px;\n  &:after {\n    content: '~';\n  }\n}\n\n.#{$namespace}-calendar-content {\n  position: relative;\n  height: 224px;\n  box-sizing: border-box;\n  .cell {\n    cursor: pointer;\n    &:hover {\n      color: $calendar-hover-color;\n      background-color: $calendar-hover-background-color;\n    }\n    &.active {\n      color: $calendar-active-color;\n      background-color: $calendar-active-background-color;\n    }\n    &.in-range,\n    &.hover-in-range {\n      color: $calendar-in-range-color;\n      background-color: $calendar-in-range-background-color;\n    }\n    &.disabled {\n      cursor: not-allowed;\n      color: $disabled-color;\n      background-color: $disabled-background-color;\n    }\n  }\n}\n\n.#{$namespace}-calendar-week-mode {\n  .#{$namespace}-date-row {\n    cursor: pointer;\n    &:hover {\n      background-color: $calendar-hover-background-color;\n    }\n    &.#{$namespace}-active-week {\n      background-color: $calendar-in-range-background-color;\n    }\n    .cell {\n      &:hover {\n        color: inherit;\n        background-color: transparent;\n      }\n      &.active {\n        color: inherit;\n        background-color: transparent;\n      }\n    }\n  }\n}\n\n.#{$namespace}-week-number {\n  opacity: 0.5;\n}\n\n.#{$namespace}-table {\n  table-layout: fixed;\n  border-collapse: separate;\n  border-spacing: 0;\n  width: 100%;\n  height: 100%;\n  box-sizing: border-box;\n  text-align: center;\n\n  th {\n    padding: 0;\n    font-weight: 500;\n    vertical-align: middle;\n  }\n  td {\n    padding: 0;\n    vertical-align: middle;\n  }\n}\n\n.#{$namespace}-table-date {\n  td,\n  th {\n    height: 32px;\n    font-size: 12px;\n  }\n\n  .today {\n    color: $today-color;\n  }\n  .cell.not-current-month {\n    color: #ccc;\n    background: none; // cover the in-range style\n  }\n}\n\n.#{$namespace}-time {\n  flex: 1;\n  width: 224px;\n  background: #fff;\n  & + & {\n    border-left: 1px solid $border-color;\n  }\n}\n.#{$namespace}-calendar-time {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n}\n.#{$namespace}-time-header {\n  @extend .#{$namespace}-calendar-header;\n  border-bottom: 1px solid $border-color;\n}\n\n.#{$namespace}-time-content {\n  height: 224px;\n  box-sizing: border-box;\n  overflow: hidden;\n}\n\n.#{$namespace}-time-columns {\n  display: flex;\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n}\n\n.#{$namespace}-time-column {\n  flex: 1;\n  position: relative;\n  border-left: 1px solid $border-color;\n  text-align: center;\n\n  &:first-child {\n    border-left: 0;\n  }\n  .#{$namespace}-time-list {\n    margin: 0;\n    padding: 0;\n    list-style: none;\n    &::after {\n      content: '';\n      display: block;\n      height: 32 * 6px;\n    }\n  }\n  .#{$namespace}-time-item {\n    cursor: pointer;\n    font-size: 12px;\n    height: 32px;\n    line-height: 32px;\n    &:hover {\n      color: $time-hover-color;\n      background-color: $time-hover-background-color;\n    }\n    &.active {\n      color: $time-active-color;\n      background-color: $time-active-background-color;\n      font-weight: 700;\n    }\n    &.disabled {\n      cursor: not-allowed;\n      color: $disabled-color;\n      background-color: $disabled-background-color;\n    }\n  }\n}\n\n.#{$namespace}-time-option {\n  cursor: pointer;\n  padding: 8px 10px;\n  font-size: 14px;\n  line-height: 20px;\n  &:hover {\n    color: $time-hover-color;\n    background-color: $time-hover-background-color;\n  }\n  &.active {\n    color: $time-active-color;\n    background-color: $time-active-background-color;\n    font-weight: 700;\n  }\n  &.disabled {\n    cursor: not-allowed;\n    color: $disabled-color;\n    background-color: $disabled-background-color;\n  }\n}\n","// Body\n$body-bg: #121212;\n\n// Typography\n@import url('https://fonts.googleapis.com/css?family=Lato');\n$ff-regular: 'Lato regular', sans-serif;\n$ff-light: 'Lato light', sans-serif;\n$ff-bold: 'Lato bold', sans-serif;\n$font-size-base: 0.9rem;\n$line-height-base: 1.6;\n\n// Colors\n$blue: #3490dc;\n$indigo: #6574cd;\n$purple: #9561e2;\n$pink: #f66d9b;\n$red: #e3342f;\n$orange: #f6993f;\n$yellow: #ffed4a;\n$green: #38c172;\n$teal: #4dc0b5;\n$cyan: #6cb2eb;\n$violet-light: #826eff;\n$color-primary: #8687E7;\n$color-bg: #363636;\n$color-bg-item: #272727;\n$color-border: #979797;\n$color-input-text: #cecece;\n"],"sourceRoot":""}]);
