wBAAA,CACA,wBAAA,CAAA,qBAAA,CACA,2BAAA,CAAA,4BAAA,CADA,kBAAA,CAJA,kBC/DG,CDgEH,iBAAA,CACA,mBAAA,CAAA,mBAAA,CAAA,YAAA,CAGA,yBAAA,CAAA,qBAAA,CACA,WAAA,CAHA,4BAAA,CAIA,UAVR,CAYQ,6DACI,cAVZ,CAcI,uDAGI,wBAAA,CACA,wBAAA,CAAA,qBAAA,CACA,2BAAA,CAAA,4BAAA,CADA,kBAAA,CAHA,iBAAA,CACA,mBAAA,CAAA,mBAAA,CAAA,YAAA,CAGA,yBAAA,CAAA,qBAAA,CAFA,4BAAA,CAIA,eAAA,CACA,cAAA,CAFA,WAVR,CAeI,yDACI,kBAbR,CAiBA,6BACI,aAdJ,CAiBA,gDACI,iCCpHO,CDsHP,cAAA,CADA,eAbJ,CAiBA,kDACI,cAAA,CACA,eAdJ","sourcesContent":["\n@import \"../../../scss/variables\";\n\n.overflow-hidden {\n    overflow: hidden;\n}\n\n.scroll-container {\n    overflow-x: auto;\n    /* width: 1580px; */\n    height: 67px;\n    overflow-y: hidden;\n\n    &::-webkit-scrollbar {\n        width: 3px !important;\n        height: 3px !important;\n    }\n\n    &::-webkit-scrollbar-track {\n        -webkit-box-shadow: inset 0 0 3px $color-bg-item;\n    }\n\n    &::-webkit-scrollbar-thumb {\n        background-color: $color-border;\n        outline: 1px solid $color-input-text;\n    }\n}\n\n#calendar__date {\n    background-size: 100%;\n    font-family: $ff-light;\n    text-align: center;\n    color: $color-input-text;\n    text-shadow: 0 0 20px $color-primary, 0 0 20px rgba(10, 175, 230, 0);\n\n    .date {\n        letter-spacing: 0.1em;\n        font-size: 14px;\n    }\n}\n\n.calendar__header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 10px 0;\n\n\n    .chevron_left {\n        border: none;\n        outline: none;\n        background: transparent;\n        transform: rotate(90deg);\n        color: $color-border;\n        transition: all .3s;\n\n        &:hover {\n            cursor: pointer;\n            color: lighten($color-border, 30);\n            transition: all .3s;\n        }\n    }\n\n    .chevron_right {\n        border: none;\n        outline: none;\n        background: transparent;\n        transform: rotate(-90deg);\n        color: $color-border;\n        transition: all .3s;\n\n        &:hover {\n            cursor: pointer;\n            color: lighten($color-border, 30);\n            transition: all .3s;\n        }\n    }\n}\n\n.calendar-dates {\n    min-width: 100%;\n    width: 1580px;\n    display: flex;\n    justify-content: space-between;\n    align-items: flex-start;\n\n    .calendar-dates__item {\n        background: $color-bg;\n        border-radius: 4px;\n        display: flex;\n        justify-content: space-around;\n        align-items: center;\n        flex-direction: column;\n        height: 52px;\n        width: 39px;\n\n        &:hover {\n            cursor: pointer;\n        }\n    }\n\n    .calendar-dates__data {\n        border-radius: 4px;\n        display: flex;\n        justify-content: space-around;\n        align-items: center;\n        flex-direction: column;\n        padding: 5px;\n        min-height: 100%;\n        min-width: 100%;\n    }\n\n    .calendar-dates__active {\n        background: $violet-light;\n    }\n}\n\n.error-text {\n    color: $red\n}\n\n.calendar-dates__item-day_week {\n    font-family: $ff-light;\n    font-weight: 100;\n    font-size: 14px;\n}\n\n.scroll-container__overflow-auto {\n    max-width: 100%;\n    overflow-x: auto;\n}\n\n","// Body\n$body-bg: #121212;\n\n// Typography\n@import url('https://fonts.googleapis.com/css?family=Lato');\n$ff-regular: 'Lato regular', sans-serif;\n$ff-light: 'Lato light', sans-serif;\n$ff-bold: 'Lato bold', sans-serif;\n$font-size-base: 0.9rem;\n$line-height-base: 1.6;\n\n// Colors\n$blue: #3490dc;\n$indigo: #6574cd;\n$purple: #9561e2;\n$pink: #f66d9b;\n$red: #e3342f;\n$orange: #f6993f;\n$yellow: #ffed4a;\n$green: #38c172;\n$teal: #4dc0b5;\n$cyan: #6cb2eb;\n$violet-light: #826eff;\n$color-primary: #8687E7;\n$color-bg: #363636;\n$color-bg-item: #272727;\n$color-border: #979797;\n$color-input-text: #cecece;\n"],"sourceRoot":""}]);
